import Vue from 'vue';
import VueRouter from 'vue-router';
import Default from './components/default.vue';
import ProjectList from './components/project/list.vue';
import ProjectMain from './components/project/main.vue';
import ProjectKanban from './components/project/kanban.vue';
import ProjectUsers from './components/project/users.vue';
import ProjectCalendar from './components/project/calendar.vue';
import ProjectMap from './components/project/location.vue';
import es6 from 'es6-promise/auto';
import Axios from 'axios';
import StringProtoTypes from '../uitls/string-prototypes.js';
import vueCalendar from "vue2-simple-calendar";
import L from 'leaflet'
Vue.use(VueRouter);


Vue.use(vueCalendar,{
  firstDay: 0,
  showLimit: 3,
  locale: 'ko',
  fullDayNames: false,
  fullMonthNames: false,
  componentName: 'vue-calendar',
  languages:{ 
  ko: {
    showMore: 'Toon meer',
    dayNameShort: ['일', '월', '화', '수', '목', '금', '토'],
    dayNameLong: ['일요일', '월요일', '화요일', '수요일', '목요일', '금요일', '토요일'],
    monthNameShort: ['1월', '2월', '3월.', '4월', '5월', '6월', '7월', '8월', '9월', '10월', '11월', '12월'],
    monthNameLong:  ['1월', '2월', '3월.', '4월', '5월', '6월', '7월', '8월', '9월', '10월', '11월', '12월']
  }}
});
console.log(vueCalendar);
const routes = [
  { path: '/', component: Default, name: 'Default'},
  { path: '/project/list', component: ProjectList, name: 'ProjectList'},
  { path: '/project/main', component: ProjectMain, name: 'ProjectMain'},
  { path: '/project/kanban', component: ProjectKanban, name: 'ProjectKanban'},
  { path: '/project/users', component: ProjectUsers, name: 'ProjectUsers'},
  { path: '/project/calendar', component: ProjectCalendar, name: 'ProjectCalendar'},
  { path: '/project/map', component: ProjectMap, name: 'ProjectMap'}

];

const router = new VueRouter({
  routes // short for `routes: routes`
});

new Vue({
  router,
  // watch:{
 
  //   $route(to, from){
    
  //     this.$children.forEach(element => {
  //       try{ 
  //         console.log("watch");
  //         console.log(element);
  //         element.init();
  //       }catch(ex){
  //         console.log(ex);
  //       }
      
  //     });
  //   }
  // } ,
  mounted() {
  //  console.log("root mounted");
    this.fnMapInit("map-lnb");
    this.fnAddMarkerToMap();
  },
  data:function(){
    return {
        mapInfo:{
          map:null,
          layers:[],
          layerInfo:{}
      }
    }
  },
  methods:{
    fnMapInit:function(paramMapId){
      let baseMap={id:"baseMap",layer:L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',{})};
      this.mapInfo.map=L.map(paramMapId,{
        center:[36.5,127.5],
        zoom:6,
        zoomControl:false,
        layers:[baseMap.layer]
    });
    },
    fnAddMarkerToMap:function(){
      L.marker([37.5, 126.5]).addTo( this.mapInfo.map);
      L.marker([34.9, 128.9]).addTo( this.mapInfo.map);
    
    }
  }
}).$mount('#app');



