<template>
  <div>
    <div class="row no-gutters widget-2 shadow-base">
      <div class="col-sm-12 col-lg-12 p-0">
        <div class="card shadow-base">
          <div class="card-header bg-transparent">
            <h6 class="card-title"><i class="ion-ios-filing-outline tx-24 va-m"></i> 인천신항 프로젝트</h6>
          <div role="group" aria-label="Basic example" class="btn-group tabbar">
                            <a href="#/project/main" class="btn ">
                            <i class="ion-ios-information-outline tx-16  va-m"></i> 프로젝트 정보
                            </a>
                            <a href="#/project/kanban" class="btn ">
                                <i class="ion-document tx-16  va-m"></i> 성과품 관리
                            </a>
                            <a href="#/project/users" class="btn ">
                                <i class="ion-ios-people-outline tx-24  va-m"></i> 프로젝트 멤버 관리
                            </a>
                            <a href="#/project/calendar" class="btn active">
                            <i class="ion-ios-calendar-outline tx-16  va-m"></i> 일정 관리
                            </a>
                            <a href="#" class="btn">
                                <i class="ion-ios-location-outline tx-16  va-m"></i> 현장 위치
                            </a>
                        </div>

          </div>
          <div class="card-body  ht-750">

               <vue-calendar
                :show-limit="3"
                :events="events"
                :height="'100%'"
                @event-clicked="fnOnClickEvent"
                @day-clicked="fnOnClickDay"
                >
                </vue-calendar>
          </div>
        </div>



      </div>
    </div>



  </div>
</template>
<script>
  export default {
      data(){
          return {
              events:[]
          }
      },
      methods:{
          fnOnClickEvent:function(event){
              console.log(event);
          },
          fnOnClickDay:function(day){
                console.log(day);
          }
      }
  }
</script>


