<template>
    <div>
        <div class="row no-gutters widget-2 shadow-base">
             <div class="col-sm-12 col-lg-12">
                 <div class="card">
                   <div class="card-header bd-b-color-gray-lighter">
                       
                        <h6 class="card-title"><i class="ion-ios-filing-outline tx-24 va-m"></i> 인천신항 프로젝트</h6>
                       <div role="group" aria-label="Basic example" class="btn-group tabbar">
                            <a href="#/project/main" class="btn active">
                            <i class="ion-ios-information-outline tx-16  va-m"></i> 프로젝트 정보
                            </a>
                            <a href="#/project/kanban" class="btn ">
                                <i class="ion-document tx-16  va-m"></i> 성과품 관리
                            </a>
                            <a href="#/project/users" class="btn ">
                                <i class="ion-ios-people-outline tx-24  va-m"></i> 프로젝트 멤버 관리
                            </a>
                            <a href="#/project/calendar" class="btn ">
                            <i class="ion-ios-calendar-outline tx-16  va-m"></i> 일정 관리
                            </a>
                            <a href="#" class="btn">
                                <i class="ion-ios-location-outline tx-16  va-m"></i> 현장 위치
                            </a>
                        </div>
                   </div>
                    <div class="card-body">
                        
                        <div class="row tx-center">
                            <div class="col-lg-6">
                                <div class="card-header pd-0 mg-b-10 btn-group-xs bd-0 ">
                                    <h6 class="tx-14 tx-info tx-left"><i class="ion-android-arrow-dropright-circle tx-14"></i> 프로젝트 정보</h6>
                                    <button class="btn btn-info">더보기</button>
                                </div>
                                <table class="table table-bordered table-mid tx-13 tx-gray-700 bd">
                                    <thead>
                                    <tr class="bg-gray-100 tx-11 tx-uppercase tx-gray-800">
                                        <th class="wd-20p tx-center">구분</th>
                                        <th class="wd-80p tx-left">정보</th>
                                    </tr>
                                    </thead>
                                    <tbody>
                                        <tr><td>프로젝트명</td><td class="tx-left">인천신항프로젝트</td></tr>
                                        <tr><td>신청자명</td><td class="tx-left">홍길동</td></tr>
                                        <tr><td>유형</td><td class="tx-left">프로젝트 유형 출력</td></tr>
                                        <tr><td>소속기관</td><td class="tx-left">소속기관명 출력</td></tr>
                                        <tr><td>사업개요</td><td class="tx-left">사업개요 출력</td></tr>
                                        <tr><td>사업비</td><td class="tx-left">1,000,000</td></tr>
                                        <tr><td>프로젝트기간</td><td class="tx-left">2021.02.01 ~ 2021.12.31</td></tr>
                                        <tr><td>등록신청 사유</td><td class="tx-left">사용자가 입력한 등록신청 사유 출력</td></tr>
                                        <tr><td>시설물명</td><td class="tx-left">대상 시설물명 출력</td></tr>
                                        <tr><td>항명</td><td class="tx-left">프로젝트 대상 항구명 출력</td></tr>
                                        <tr><td>세부항명</td><td class="tx-left">세부항명 출력</td></tr>
                                    </tbody>
                                </table>

                            </div>
                            <div class="col no-change-month" >
                                 <div class="card-header  pd-0 mg-b-10 btn-group-xs bd-0 ">
                                    <h6 class="tx-14 tx-info tx-left"><i class="ion-android-arrow-dropright-circle tx-14"></i> 프로젝트 일정</h6>
                                    <button class="btn btn-info">더보기</button>
                                </div>
                                <vue-calendar
                            :show-limit="3"
                            :events="events"
                            @event-clicked="eventClicked"

                            ></vue-calendar>

                            </div>
                        </div>
                        
                        <div class="row  tx-center">
                            <div class="col">
                                <div class="card-header pd-0 mg-b-10 btn-group-xs bd-0 ">
                                    <h6 class="tx-14 tx-info tx-left"><i class="ion-android-arrow-dropright-circle tx-14"></i> 성과품 정보</h6>
                                    <button class="btn btn-info">더보기</button>
                                </div>
                                <table class="table table-bordered table-mid tx-13 tx-gray-700 bd">
                                    <thead>
                                         <tr class="bg-gray-100 tx-11 tx-uppercase tx-gray-800">
                                             <th class="tx-center">구분</th>
                                             <th class="tx-center">WIP</th>
                                             <th class="tx-center">Shared</th>
                                             <th class="tx-center">Publish</th>
                                             <th class="tx-center">Archive</th>
                                         </tr>

                                    </thead>
                                    <tbody>
                                        <tr>
                                            <td>성과품(개)</td><td>19</td><td>10</td><td>10</td><td>5</td>
                                        </tr>
                                        <tr>
                                            <td>파일(개)</td><td>100</td><td>50</td><td>37</td><td>20</td>
                                        </tr>
                                        <tr>
                                            <td colspan="5" class="text-right"> 총 44건 207개의 파일</td>
                                        </tr>
                                    </tbody>
                                </table>

                                <div class="card-header pd-0 mg-b-10 btn-group-xs bd-0 ">
                                    <h6 class="tx-14 tx-info tx-left"><i class="ion-android-arrow-dropright-circle tx-14"></i> 참여자 정보</h6>
                                    <button class="btn btn-info">더보기</button>
                                </div>
                                
                                <table class="table table-bordered table-mid tx-13 tx-gray-700 bd">
                                    <thead>
                                         <tr class="bg-gray-100 tx-11 tx-uppercase tx-gray-800">
                                             <th class="tx-center">번호</th>
                                             <th class="tx-center">소속기관</th>
                                             <th class="tx-center">이름</th>
                                             <th class="tx-center">아이디</th>
                                             <th class="tx-center">등록일</th>
                                         </tr>

                                    </thead>
                                    <tbody>
                                        <tr v-for="item in 10" v-bind:key="item">
                                            <td>{{item}}</td><td>기관명출력</td><td>홍길동</td><td>아이디출력</td><td>2021.04.15</td>
                                        </tr>
                                       
                                    </tbody>
                                </table>
                            </div>
                            <div class="col">
                                <div class="card-header pd-0 mg-b-10 btn-group-xs bd-0 ">
                                    <h6 class="tx-14 tx-info tx-left"><i class="ion-android-arrow-dropright-circle tx-14"></i> 현장 위치</h6>
                                    <button class="btn btn-info">더보기</button>
                                </div>
                                <div id="map-project" class="map-project">

                                </div>
                            </div>     
                        </div>
                   
                    </div>
                 </div>
             </div>
        </div>
    </div>
</template>
<script>
import L from 'leaflet'
export default {
    data() {
        return {
            mapInfo:{
                map:null,
                layers:[],
                layerInfo:{}
            }
        }
    },
    mounted(){
        this.fnMapInit("map-project");
    },
    methods:{
        fnMapInit:function(paramMapId){
            let baseMap={id:"baseMap",layer:L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',{})};
            this.mapInfo.map=L.map(paramMapId,{
                center:[37.45971211802409, 126.62558099789067],
                zoom:15,
                zoomControl:false,
                layers:[baseMap.layer]
            });
            L.marker([37.45971211802409, 126.62558099789067]).addTo( this.mapInfo.map);

        }
    }
}
</script>


